import { Component } from '@angular/core';
import { LoaderService, AuthService, AlertService } from './core/services';
import { Title } from '@angular/platform-browser';
import { debounceTime } from 'rxjs/operators';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'vybes-front';

  showLoader: boolean
  constructor(
    private loaderSrvc: LoaderService,
    private authSrvc:AuthService,
  ) {
    // check for user data on browser storage
    this.authSrvc.checkForUserExistance()

    // listen to loader subject to trigger its display
    this.loaderSrvc.showLoader$.pipe(debounceTime(100)).subscribe((value) => {
      this.showLoader = value
    })
  }

}
